.movie {
  width: 50%;
  float: left;
  padding: 20px;
  padding-bottom: 50px;
  box-sizing: border-box;
  position: relative;
}
.movie img {
  width: 200px;
  position: absolute;
  left: 50px;
  bottom: 80px;
  box-shadow: 0px 5px 15px 3px rgba(0, 0, 0, 0.6);
}
.movie .movie_data {
  background: #fff;
  padding: 30px;
  padding-left: 260px;
  box-sizing: border-box;
  height: 300px;
  box-shadow: 0px 5px 15px 5px rgba(0, 0, 0, 0.3);
}
.movie .movie_data .movie_title {
  font-size: 30px;
}
.movie .movie_data .movie_year {
  margin-top: 10px;
  font-size: 20px;
}
.movie .movie_data .movie_summary {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  word-wrap: break-word;
  font-size: 16px;
}
.movie .movie_data .genres {
  margin: 20px 0;
}
.movie .movie_data .genres .genres_genre {
  list-style: none;
  display: inline-block;
  margin-right: 10px;
}
